.permission-screen {
  min-height: 100vh;
  width: 100vw;
  padding: 24px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  gap: 16px;
  overflow: hidden;
}

.permission-screen h2 {
  font-size: 1.6rem;
  line-height: 1.3;
  margin-bottom: 8px;
}

.permission-screen p {
  max-width: 320px;
  font-size: 1rem;
  line-height: 1.5;
  margin-bottom: 16px;

  /* üëá IMPORTANT : retours √† la ligne propres */
  word-wrap: break-word;
  overflow-wrap: break-word;
  white-space: normal;
}

.permission-screen button {
  width: 100%;
  max-width: 280px;
  padding: 12px 16px;
  font-size: 1rem;
  border-radius: 12px;
  border: none;
  cursor: pointer;
}

.permission-screen button:first-of-type {
  background: #4f7cff;
  color: white;
}

.permission-screen button:last-of-type {
  background: transparent;
  color: #4f7cff;
  border: 1px solid #4f7cff;
}

/* carte m√©t√©o */
.current-weather {
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(1rem);
  border-radius: 1.5rem;
  padding: 0.875rem 0.125rem 0.125rem;
  width: 100%;
  max-width: 18.75rem;
  box-shadow: 0 1.25rem 2.5rem rgba(0, 0, 0, 0.35);
  text-align: center;
  border: 0.125rem solid rgba(255, 255, 255, 0.15);
}

/* carte m√©t√©o infos */
.current-weather-infos {
  backdrop-filter: blur(1rem);
  padding: 0.875rem 0.125rem 0.125rem;
  width: 100%;
  max-width: 25rem;
  text-align: center;
}

.condition,
.condition-infos {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
}

.condition p {
  font-size: 2rem;
  margin: 0;
  color: aliceblue;
}

.condition img {
  width: 3rem;
  height: 3rem;
}

.condition-infos p {
  font-size: 1rem;
  margin: 0;
  margin-top: 0.3125rem;
  color: aliceblue;
}

.condition-infos img {
  width: 1.5rem;
  height: 1.5rem;
}

.datetime {
  display: flex;
  justify-content: space-around;
}

/* ville */
.current-weather h2 {
  margin: 0;
  font-size: 1.3rem;
  font-weight: 600;
  letter-spacing: 0.03125rem;
}

/* date / heure */
.current-weather p:first-of-type {
  margin-top: 0.625rem;
  font-size: 1.2rem;
}

/* temp√©rature principale */
.current-weather p:nth-of-type(1) {
  font-size: 1.3rem;
  font-weight: 700;
  margin: 0.625rem 0;
  color: rgb(147, 243, 198);
}

/* ressenti */
.current-weather p:nth-of-type(2) {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.85);
}

/* descriptions m√©t√©o */
.current-weather p:nth-of-type(3),
.current-weather p:nth-of-type(4) {
  font-size: 0.8rem;
  margin: 0.625rem 0;
  text-transform: capitalize;
}

.current-weather p:nth-of-type(5) {
  font-size: 0.8rem;
  margin: 0.625rem 0 0;
  text-transform: capitalize;
}

/* update */
.current-weather p:last-of-type {
  margin-top: 0.625rem;
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.6);
}

/* weather card */
.weather-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.weather-card h2:first-child {
  margin-top: 4rem;
}

.weather-card p:first-of-type {
  font-size: 0.9rem;
  margin-bottom: 0;
}

.weather-card p:nth-of-type(n+2) {
  font-size: 0.75rem;
  margin-bottom: 0;
}

.weather-card img {
  width: 3.2rem;
  height: 3.2rem;
  margin-top: 0.375rem;
  margin-bottom: 0.9375rem;
}

/* boutons bas */
.bottom-buttons {
  margin-top: auto;
  display: flex;
  justify-content: space-around;
  padding: 1.25rem;
  border-top: 0.0625rem solid #ccc;
  gap: 1.25rem;
}

.bottom-buttons button {
  padding: 0.625rem 1.25rem;
  font-size: 1rem;
  cursor: pointer;
  border-radius: 0.3125rem;
  border: none;
  background-color: #4caf50;
  color: white;
  transition: background-color 0.3s;
}

.bottom-buttons button:hover {
  background-color: #45a049;
}

/* boutons head */
.bottom-head {
  display: flex;
  justify-content: space-around;
  margin-bottom: 0.625rem;
  gap: 1.25rem;
}

.bottom-head button {
  font-size: 1rem;
  cursor: pointer;
  border-radius: 0.3125rem;
  border: none;
  background-color: transparent;
  color: white;
  transition: background-color 0.1s;
}

/* .bottom-head button:hover {
  background-color: #dde2dd;
} */

@supports (height: 100dvh) {
  body,
  .permission-screen {
    height: 100dvh;
  }
}
